import UserServices from '@/services/UserServices'
import InboxService from '@/services/InboxService'
import PaymentService from '@/services/PaymentService'
import DashboardHeader from '@/components/DashboardHeader.vue'
import { ResponsiveDirective } from 'vue-responsive-components'

var $ = require('jQuery')

export default {

  data () {
    return {
      // this array shows up in the inbox to the left
      inboxMessages: [],
      complete: true,
      stripeOptions: {
      },
      credits: 0,
      quoteIdClickedOn: 0
    }
  },
  components: {
    DashboardHeader
  },
  directives: {
    responsive: ResponsiveDirective
  },
  methods: {
    async getAmountOfCredits () {
      try {
        const userExtracted = this.$store.getters.getUserInfo
        const response = await PaymentService.getAmountOfCredits(userExtracted.id)
        this.credit = response.data.credits
      } catch (error) {
        if (error) throw error
      }
    },
    // Loads the locked quote requests into the inbox view on the left
    async getLockedInboxMessages () {
      try {
        const userExtracted = this.$store.getters.getUserInfo
        const userId = userExtracted.id
        const response = (await UserServices.getInboxMessages(userId)).data.inboxMessages
        this.inboxMessages = response

        console.log(`\n\nthe inxboxMssages are ${JSON.stringify(this.inboxMessages)}\n\n`) // TESTING

        // CLEARING OUT THE UNLOCKED QUOTE REQUESTS
        var filtered = this.inboxMessages.filter(function (elem) {
          if (elem.locked === true) {
            return true
          } else {
            return false
          }
        })
        this.inboxMessages = filtered

        if (this.inboxMessages !== undefined || this.inboxMessages.length > 0) {
          this.quoteIdClickedOn = this.inboxMessages[0].id
          const msg = this.inboxMessages[0]
          this.showMessage(msg)
        }
      } catch (error) {
        if (error) throw error
      }
    },
    async getUnlockedinboxMessages () {
      try {
        console.log(`\nAre we filtering the inbox messages ?\n`) // TESTING

        const userExtracted = this.$store.getters.getUserInfo
        const userId = userExtracted.id
        const response = (await UserServices.getInboxMessages(userId)).data.inboxMessages
        console.log(`\nThe response for : ${response}\n`) // TESTING
        this.inboxMessages = response

        console.log(`\n\nljkdnjlsnd;\n\n`) // TESTING

        var filtered = this.inboxMessages.filter(function (elem) {
          if (elem.locked === false) {
            return true
          } else {
            return false
          }
        })

        this.inboxMessages = filtered

        if (this.inboxMessages !== undefined || this.inboxMessages.length > 0) {
          this.inboxMessages = filtered
          const msg = this.inboxMessages[0]
          this.showMessage(msg)
        }
      } catch (error) {
        if (error) throw error
      }
    },
    async unlockInboxMsg () {
      try {
        const userExtracted = this.$store.getters.getUserInfo
        const quoteIdToBeUnlocked = this.quoteIdClickedOn

        var credits = (await PaymentService.getAmountOfCredits(userExtracted.id)).data.credits

        if (credits === null) {
          credits = 0
        }

        if (credits >= 3) {
          console.log(`\nunlocking credits ${quoteIdToBeUnlocked}\n`) // TESTING
          await UserServices.unlockInboxMsg(quoteIdToBeUnlocked)
          await InboxService.relayEmail({
            quoteIdToBeUnlocked: quoteIdToBeUnlocked,
            userEmail: userExtracted.email
          })
          await PaymentService.subtractCredits(userExtracted.id, 3)

          this.$modal.hide('unlocking-prompt')
          this.getLockedInboxMessages()
        }
      } catch (error) {
        console.log(`\nAn error occurred in the unlockInboxMsg ${error}\n`) // TESTING
        if (error) throw error
      }
    },
    async promptToUnlock () {
      this.$modal.show('unlocking-prompt')
    },
    // This function also sets the clicked on variable
    // of the message
    async showMessage (msg) {
      if (msg.locked === true) {
        $('#message-pane').removeClass('is-hidden')
        $('#message-pane-unlocked').addClass('is-hidden')
        $('.card').removeClass('active')
        $('.message .address .name').text(msg.from)
        $('.message .address .email').text(msg.to)
        $('.message .content').html('')
      } else {
        $('#message-pane-unlocked').removeClass('is-hidden')
        $('#message-pane').addClass('is-hidden')
        $('.card').removeClass('active')
        var msgBody = '<p>' +
                        msg.message +
                        '</p>' +
                        '<br>' +
                        '<p>' +
                    $('.message .content').html(msgBody)
      }
      this.quoteIdClickedOn = msg.id
      console.log(`\nThis quote id is : ${this.quoteIdClickedOn}\n`) // TESTING
    }
  },
  mounted () {
    this.getLockedInboxMessages()
  }
}



