n .container-fluid{\n  background-color:white;\n  opacity: .85;\n  padding:50px ;\n}\n\n/* Green/purple design background */\n .green {\n  position: relative;\n  width: 100%;\n  background-image: url('BACKGROUND.png');\n  background-size: cover;\n  background-position: center;\n}\n\n.feature-item {\n  margin: 20px;\n}\n\n/* .green .col-sm {\n  background-color:white;\n  opacity: .80;\n  padding:50px ;\n}  */\n\ninput[type=text] {\n  width: 100%;\n  padding: 12px 20px;\n  margin: 8px;\n  box-sizing: border-box;\n}\n\n/* most important heading should be in h1 */\nh1,\nh2 {\n  letter-spacing: 4px;\n  text-transform: uppercase;\n  font-family:  'Open Sans', sans-serif;\n  font: 35px 'Open Sans', sans-serif;\n  color: #311c63;\n  text-align:center;\n}\n\nh3  {\n  letter-spacing: 2px;\n  font-style: medium;\n  font: 30px 'Lato';\n  color: #311c63;\n  text-align:center;\n}\n\nh4  {\n  font-style: medium;\n  font: 30px 'Lato';\n  color: #2d1e35;\n  text-align:center;\n}\n\nh5  {\n  font-style: medium;\n  font: 25px 'Roboto';\n  color: #2d1e35;\n  text-align:center;\n}\n\n\np {\n  font: 'Open Sans', sans-serif;\n  font-size: 24px;\n  line-height: 1.5;\n  color: #2d1e35;\n  margin-bottom: 20px;\n  text-align: center;\n}\n\n.center {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 50%;\n  animation-name: moveInLeft;\n  animation-duration: 2s;\n}\n\n/* logo animation */\n@keyframes moveInLeft {\n  0% {\n    opacity: 0;\n    transform: translateX(-100px);\n  }\n\n  80% {\n    transform: translateX(20px);\n  }\n\n  100% {\n    opacity: 1;\n    transform: translate(0);\n  }\n}\n\n\na {\ncolor: #311c63;\n-webkit-transition: all .35s;\ntransition: all .35s;\n}\n\na:hover, a:focus {\ncolor: #6a2c86;\n}\n\nhr {\nmax-width: 100px;\nmargin: 25px auto 0;\nborder-width: 1px;\nborder-color: rgba(34, 34, 34, 0.1);\n}\n\nhr.light {\nborder-color: white;\n}\n\n.btn-signup {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-login {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-signup-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-login-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-signup-outline:hover, .btn-signup-outline:focus, .btn-signup-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn-login-outline:hover, .btn-login-outline:focus, .btn-login-outline.active {\n  color: white;\n  border-color: #088a12;\n  background-color: #088a12;\n}\n\n.btn-xl {\nfont-size: 11px;\npadding: 15px 45px;\n}\n\n.icon {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 115px;\n  height:100px;\n}\n\n\nsection {\n  padding: 200px 0px 200px;\n}\n\nsection.services {\n  padding: 100px 75px 100px;\n}\n\n\nform {\n  width:300px;\n  font-size:14pt;\n  display: inline-block;\n}\n\n/* ----footer -----*/\n\nfooter {\n  padding: 30px 0px 10px;\n  text-align: left;\n  color: black(255, 255, 255, 0.8);\n  background-color: #d3d3d3;\n}\n\n/* #copy-rights {\n  background-color:#361555;\n} */\n\n#contact, p {\n  font-size: 15px;\n  margin: 0;\n}\n\n#contact h4 {\n  font-size: 18px;\n  margin: 0;\n}\n\nfooter .row {\n  padding: 10px;\n}\n\n.bg-primary {\n  background: #482774;\n  background: -webkit-gradient(linear, left top, left bottom, from(#4b1552), to(#250d41));\n  background: linear-gradient(#4b1552, #250d41);\n}\n\n.text-primary {\n  color: #fdcc52;\n}\n\n.btn-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n\n}\n\n.btn-outline:hover, .btn-outline:focus, .btn-outline:active, .btn-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn {\n  border-radius: 300px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-xl {\n  font-size: 11px;\n  padding: 15px 45px;\n}\n\n.btn-default {\n  background-color: #361555;\n  color:#FFF;\n  border-color: #2F3E48;\n}\n\n\n/* NAVBAR STYLING */\n#mainNav {\n  border-color: rgba(34, 34, 34, 0.05);\n  background-color: white;\n  -webkit-transition: all .35s;\n  transition: all .35s;\n  font-family: 'Catamaran', 'Helvetica', 'Arial', 'sans-serif';\n  font-weight: 200;\n  letter-spacing: 1px;\n  /* visibility: hidden;  */\n  opacity: 0;\n}\n\n#mainNav .navbar-brand {\n  font-family: 'Catamaran', 'Helvetica', 'Arial', 'sans-serif';\n  font-weight: 200;\n  letter-spacing: 1px;\n}\n\n#mainNav:hover, #mainNav .navbar-brand:focus {\n  color: #823E90;\n  visibility:visible;\n  opacity: 1;\n}\n\n#mainNav .navbar-toggler {\n  font-size: 12px;\n  padding: 8px 10px;\n  color: #222222;\n}\n\n#mainNav .navbar-nav > li > a {\n  font-size: 11px;\n  font-family: 'Lato', 'Helvetica', 'Arial', 'sans-serif';\n  letter-spacing: 2px;\n  text-transform: uppercase;\n}\n\n/* #mainNav .navbar-nav > li > a.active,\n#mainNav .navbar-nav > li > a:hover, {\n  color: #87C27B !important;\n  background-color: transparent;\n} */\n\n/* Header */\nheader.masthead {\n  position: relative;\n  width: 100%;\n  padding-top: 100px;\n  padding-bottom: 200px;\n  color: white;\n  background: url(\"./HEADER.png\");\n  background: url(\"./HEADER.png\"), -webkit-gradient(linear, right top, left top, from(#c7c7c7), to(#ffffff));\n  background: url(\"./HEADER.png\"), linear-gradient(to right, #e2e2e2, #ffffff);\n}\n\nul {\n  text-align: left\n}\n\n/* ------Vue Directive Responsive Design Ish ----------------------------------------------------*/\n.responsive-component.small h1, h2 {\n  letter-spacing: 1px;\n  font-size: 28px;\n  text-align:center;\n}\n\n.responsive-component.small h3 {\n  letter-spacing: 1px;\n  font-size: 25px;\n  text-align:center;\n}\n\n.responsive-component.small p{\n  width:100%;\n  font-size: 20px;\n}\n\n.responsive-component.small footer p{\n  width:100%;\n  font-size: 10px;\n}\n\n.responsive-component.small form{\n  width:100%;\n  font-size: 12px;\n}\n\n"],"sourceRoot":""}]);

