import DashboardHeader from '@/components/DashboardHeader.vue'
import BillingTab from '@/components/SettingsPageBillingTab.vue'
import UserServices from '@/services/UserServices'

import SettingsService from '@/services/SettingsService'

import Tabs from 'vue-tabs-with-active-line'
import { ResponsiveDirective } from 'vue-responsive-components'

export default {
  data () {
    return {
      name: '',
      email: '',
      number: '',
      jobTitle: '',
      companyName: '',
      companyWebsite: '',
      billingAddress: '',
      about: '',
      tabs: [
        { title: 'Profile', value: 'profile1' },
        { title: 'Billing', value: 'billing3' },
        { title: 'Password', value: 'password4' }
      ],
      currentTab: 'profile1',
      user: null,
      oldPass: '',
      newPass: '',
      confirmNewPass: ''
    }
  },
  directives: {
    responsive: ResponsiveDirective
  },
  mounted () {
    this.retrieveProfileInfo()
  },
  methods: {
    handleClick (newTab) {
      this.currentTab = newTab
    },
    async updatePassword () {
      try {
        const userExtracted = this.$store.getters.getUserInfo

        const updatePassInfo = {
          email: userExtracted.email,
          oldPass: this.oldPass,
          newPass: this.newPass,
          confirmNewPass: this.confirmNewPass
        }

        const response = await SettingsService.updatePassword(updatePassInfo)
        console.log(`\nThe response for update password : ${JSON.stringify(response)}\n`) // TESTING to see if the password has been updated
      } catch (error) {
        console.log(`\nThe error caught in update password ${error}\n`) // TESTING
        if (error) throw error
      }
    },
    async updateProfile () {
      try {
        const userExtracted = this.$store.getters.getUserInfo
        const newProfileInfo = {
          name: this.name,
          email: this.email,
          number: this.number,
          jobTitle: this.jobTitle,
          companyName: this.companyName,
          companyWebsite: this.companyWebsite,
          billingAddress: this.billingAddress,
          about: this.about
        }
        const response = await SettingsService.updateProfile(userExtracted.id, newProfileInfo)
        console.log(`\nThe response for updateProfile is : ${JSON.stringify(response.data.updatedUser)}\n`) // TESTING
      } catch (error) {
        console.log(`\nThe is inside of updateProfile : ${error}\n`) // TESTING
        if (error) throw error
      }
      // TODO : Code
      console.log(`\nSends all the information of inside the v models onto the db\n`) // TESTING
    },
    async retrieveProfileInfo () {
      try {
        const userExtracted = this.$store.getters.getUserInfo
        const userInfo = await UserServices.getPublicProfileInfo(userExtracted.id)

        this.name = userInfo.data.user.name
        this.email = userInfo.data.user.email
        this.number = userInfo.data.user.number
        this.jobTitle = userInfo.data.user.jobTitle
        this.companyName = userInfo.data.user.companyName
        this.companyWebsite = userInfo.data.user.companyWebsite
        this.billingAddress = userInfo.data.user.billingAddress
        this.about = userInfo.data.user.about
      } catch (error) {
        console.log(`\nThe error message inside of the retrieveProfileInfo: ${error}\n`)
        if (error) throw error
      }
    }
  },
  components: {
    Tabs,
    DashboardHeader,
    BillingTab
  }
}



