ards {\n  \t\twidth: 400px;\n\t\tfont-size: 150%;\n  \t\tborder: 2px solid purple;\n  \t\tpadding: 20px;\n\t\tmargin-left: 10px;\n\t}\n</style>\n"],"sourceRoot":""}]);

